import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import reportWebVitals from './reportWebVitals';

class App extends React.Component
{
  constructor(props)
  {
    super(props);
    
    this.state = 
    {
      Customers: [
        {
          id: 1,
          firstName: 'James',
          lastName: 'Peterson',
          age: 22
        }, 
        {
          id: 2,
          firstName: 'John',
          lastName: 'Hernandez',
          age: 24
        }, 
        {
          id: 3,
          firstName: 'Luke',
          lastName: 'Goodman',
          age: 23
        }],
        id: 4
    }
  }

  handleSubmit(e)
  {
    e.preventDefault();
    //const newCustomer = this.newCustomer.value;
    const firstName = this.firstName.value;
    const lastName = this.lastName.value;
    const age = this.age.value;

    const newCustomer = 
    {
      id: this.state.id,
      firstName: this.firstName.value,
      lastName: this.lastName.value,
      age: Number(this.age.value)
    }

    this.setState(
      {
        Customers: [...this.state.Customers, newCustomer],
        id: this.state.id+1
      })
  }

  render()
  {
    const {Customers} = this.state;
    //const item = buyItems.map( val => <li>{val}</li> )
    
    return <div>
      <header>
        {/* <img src={image} /> */}
        <h1>Customers Management System</h1>
      </header>

      <form onSubmit={(e) => (this.handleSubmit(e))}>
        <div class="form-row">
          <div class="col"> 
            <label for="firstName">First Name</label>
            <input ref={input => this.firstName = input} type="text" class="form-control" id="firstName" placeholder="Add first name" />
          </div>
          <div class="col"> 
            <label for="lastName">Last Name</label>
            <input ref={input => this.lastName = input} type="text" class="form-control" id="lastName" placeholder="Add last name" />
          </div>
          <div class="col"> 
            <label for="age">Age</label>
            <input ref={input => this.age = input} type="number" min="18" max="120" class="form-control" id="age" placeholder="Add age" />
          </div>
          <div class="col">
            <br />
            <button class="btn btn-primary" type="submit">Submit</button>
          </div>
        </div>
      </form>

      <div className="content">
        <table className="table">
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">First Name</th>
              <th scope="col">Last Name</th>
              <th scope="col">Age</th>
              <th scope="col">Action</th>
            </tr>
          </thead>
          <tbody>
            {
              Customers.map((item) => 
              <tr key={item.id}>
                <th scope="row">{item.id}</th>
                <td>{item.firstName}</td>
                <td>{item.lastName}</td>
                <td>{item.age}</td>
              </tr>
              )
            }
          </tbody>
        </table>
        

      </div>

    </div> 
  }
}

ReactDOM.render (
    <App />,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
